  &__display-singleseries {
    display: flex;
    flex-direction: column;
    background-color: transparent;
    margin-right: 1rem;
    border-radius: 10px;
    padding: 7px;
    border: 1px solid white;

    &__element {
      font-size: 0.9rem;

      &__button {
        background: transparent;
        border: 2px solid $colar_pink;
        color: white;
        padding: 5px 20px 5px;
        border-radius: 10px;

        :hover {
          cursor: pointer;
        }
      }

      &__title {
        color: $colar_pink;
        font-weight: 600;
      }
    }

    &__elements {
      &__top {
        display: flex;
